<template>
  <div class="Signup">
    <div class="form">
      <el-card class="box-card">
        <el-image src="/static/assets/logo.png" fit="fill">
          <div slot="placeholder" class="image-slot">
            加载中
            <span class="dot">...</span>
          </div>
          <div slot="error" class="image-slot">
            <i class="el-icon-picture-outline"></i>
          </div>
        </el-image>
        <div v-if="!showform" class="finishmessage">
          <router-link to="/login">注册成功，前往登录。</router-link>
        </div>
        <el-form
          :model="ruleForm"
          status-icon
          :rules="rules"
          ref="ruleForm"
          label-width="80px"
          class="ruleForm"
          v-if="showform"
        >
          <el-form-item label="用户名" prop="username">
            <el-input type="text" v-model="ruleForm.username" auto-complete="off"></el-input>
          </el-form-item>
          <el-form-item label="密码" prop="pass">
            <el-input type="password" v-model="ruleForm.pass" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="确认密码" prop="checkPass">
            <el-input type="password" v-model="ruleForm.checkPass" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="电话" prop="tel">
            <el-input v-model="ruleForm.tel"></el-input>
          </el-form-item>
          <el-form-item label="邮箱" prop="mail">
            <el-input v-model="ruleForm.mail"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="submitForm('ruleForm')">
              <i v-show="isdoing" class="el-icon-loading"></i>注册
            </el-button>
            <el-button @click="resetForm('ruleForm')">重置</el-button>
          </el-form-item>
        </el-form>
      </el-card>
    </div>
  </div>
</template>

<script>
export default {
  name: "Signup",
  data() {
    var validatePass = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("请输入密码"));
      } else {
        if (this.ruleForm.checkPass !== "") {
          this.$refs.ruleForm.validateField("checkPass");
        }
        callback();
      }
    };
    var validatePass2 = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("请再次输入密码"));
      } else if (value !== this.ruleForm.pass) {
        callback(new Error("两次输入密码不一致!"));
      } else {
        callback();
      }
    };
    var validateUsername = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("请输入用户名"));
      } else {
        callback();
      }
    };
    var checkTel = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("请输入电话"));
      } else {
        setTimeout(() => {
          if (new RegExp(/^1[3|4|5|7|8]\d{9}$/).test(value)) {
            callback();
          } else {
            callback(new Error("请输入正确的电话"));
          }
        }, 1000);
      }
    };
    var checkMail = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("请输入邮箱"));
      } else {
        setTimeout(() => {
          if (
            new RegExp(
              /^([A-Za-z0-9_\-\.])+\@([A-Za-z0-9_\-\.])+\.([A-Za-z]{2,4})$/
            ).test(value)
          ) {
            callback();
          } else {
            callback(new Error("请输入正确的邮箱"));
          }
        }, 1000);
      }
    };
    return {
      isdoing: false,
      showform: true,
      ruleForm: {
        username: "",
        pass: "",
        checkPass: "",
        tel: "",
        mail: ""
      },
      rules: {
        username: [{ validator: validateUsername, trigger: "blur" }],
        pass: [{ validator: validatePass, trigger: "blur" }],
        checkPass: [{ validator: validatePass2, trigger: "blur" }],
        tel: [{ validator: checkTel, trigger: "blur" }],
        mail: [{ validator: checkMail, trigger: "blur" }]
      }
    };
  },
  methods: {
    submitForm(formName) {
      this.$refs[formName].validate(
        function(valid) {
          if (valid) {
            this.isdoing = true;
            this.signup();
          } else {
            console.log("error submit!!");
            return false;
          }
        }.bind(this)
      );
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },
    signup: function() {
      var url = this.$appconfig.API.signup;
      this.$http
        .get(url, {
          params: {
            username: this.ruleForm.username,
            password: this.ruleForm.pass,
            tel: this.ruleForm.tel,
            email: this.ruleForm.mail
          }
        })
        .then(
          function(response) {
            this.isdoing = false;
            if (response.data.status == 1) {
              this.open(response.data.message);
              console.log(response.data.message);
            } else {
              this.showform = false;
            }
          }.bind(this)
        )
        .catch(
          function(error) {
            this.$router.go(0);
          }.bind(this)
        )
        .then(
          function() {
            // always executed
          }.bind(this)
        );
    },
    open(message) {
      this.$notify({
        title: "消息",
        message: message,
        position: "bottom-left"
      });
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.Signup {
  width: 100%;
  height: 100%;
  background: #fff;
}
.form {
  width: 30%;
  min-width: 370px;
  height: 100%;
  background: #eee;
}
.box-card {
  width: 80%;
  margin: auto;
  transform: translate(0px, calc(15vh));
}
.ruleForm {
  width: calc(100% - 50px);
}
.el-image {
  width: 100px;
  height: 100px;
}
.image-slot {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  background: #f5f7fa;
  color: #909399;
  font-size: 20px;
}
.finishmessage {
  height: 370px;
  display: flex;
  justify-content: center;
  align-items: center;
}
</style>



