<template>
  <div class="main">
    <div class="menuone">
      <div class="empny el-icon-notebook-2" v-show="formatlistitem.length == 0">空无一物</div>
      <div
        v-for="(item, index) in formatlistitem"
        :key="index"
        style="border-bottom:1px solid rgb(224, 230, 237);"
      >
        <listitem :item="item"></listitem>
      </div>
    </div>
    <el-menu class="menutwo">
      <el-menu-item @click="createitem">
        <i class="el-icon-circle-plus" style="color:blue;font-size:24px;"></i>
        <span slot="title">添加代办事项</span>
      </el-menu-item>
    </el-menu>
    <div v-show="openform" style="position: fixed;bottom: 0px;right: 0px;width: 80%;">
      <createtodo @hidecreateform="closeform"></createtodo>
    </div>
  </div>
</template>
<script>
import listitem from "./Tablelist/Listitem";
import createtodo from "./Tablelist/Createtodo";
export default {
  components: { listitem, createtodo },
  computed: {
    listitem: function() {
      return this.$store.getters.gettodolist;
    },
    selectid: function() {
      return this.$store.getters.getcurrentid;
    },
    filterlist: function() {
      if(!this.selectid){
        return this.listitem;
      }
      return this.listitem.filter(
        function(item, index) {
          return item.type == this.selectid;
        }.bind(this)
      );
    },
    formatlistitem: function() {
      return this.filterlist.map(function(item) {
        return {
          isFinish: item.isfinish,
          label: item.content,
          time: new Date(item.createtime).toLocaleTimeString(),
          isimportant: item.isimportant,
          note: item.note,
          id: item._id
        };
      });
    }
  },
  data() {
    return {
      openform: false
    };
  },
  methods: {
    createitem: function() {
      this.openform = true;
    },
    closeform: function() {
      this.openform = false;
    }
  }
};
</script>
<style scoped>
.main {
  height: 100%;
  overflow: hidden;
}
.menuone {
  height: calc(100% - 56px);
  overflow-y: auto;
}
.menutwo {
  border-top: 1px solid rgb(224, 230, 237);
}
.empny {
  font-size: 24px;
  transform: translate(0px, 30vh);
}
</style>


