<template>
  <table>
    <tr>
      <td rowspan="2" @click="finish">
        <div class="circle" v-bind:class="{ 'el-icon-circle-check': item.isFinish }"></div>
      </td>
      <td colspan="3" v-bind:class="{ isfinish:item.isFinish }">{{item.label}}</td>
    </tr>
    <tr>
      <td>{{item.time}}</td>
      <td>
        <i class="el-icon-warning" style="color:red;font-size: 24px;" v-show="item.isimportant"></i>
      </td>
      <td>（备注：）-</td>
      <td>{{item.note}}</td>
    </tr>
  </table>
</template>
<script>
export default {
  name: "Listitem",
  props: ["item"],
  data() {
    return {};
  },
  methods: {
    finish: function() {
      var url = this.$appconfig.API.updatetodo;
      this.$http
        .get(url, {
          params: {
            id: this.item.id,
            isfinish: !this.item.isFinish
          }
        })
        .then(
          function(response) {
            if (response.data.status == 1) {
            } else {
              this.$store.commit("changefinish", this.item.id);
            }
          }.bind(this)
        )
        .catch(function(error) {}.bind(this))
        .then(
          function() {
            // always executed
          }.bind(this)
        );
    }
  }
};
</script>
<style scoped>
table {
  height: 56px;
}
.isfinish {
  text-decoration: line-through;
  color: gray;
}
.circle {
  width: 24px;
  height: 24px;
  font-size: 24px;
  border-radius: 100%;
  border: 2px solid;
  color: red;
}
</style>


