<template>
  <div>
    <div class="user">
      <div>
        <el-avatar icon="el-icon-user-solid" size="medium"></el-avatar>
      </div>
      <div style="padding-left: 20px;font-size: 20px;width: 75%;">{{ username }}</div>
      <div class="el-icon-setting" style="font-size: 24px;"></div>
    </div>
    <div style="height: calc(100vh - 90px);overflow-y: auto;">
      <el-menu @select="menuselect">
        <el-menu-item
          v-for="(item, index) in classify"
          :key="index"
          :index="index.toString()"
          style="font-size:18px;"
          @click="menuitemclk"
        >
          <i class="el-icon-s-claim" v-bind:style="{ color: item.color }"></i>
          <span slot="title">{{ item.label }}</span>
          <span style="float:right;">{{ classifycount[item._id] }}</span>
        </el-menu-item>
        <el-menu-item @click="createclassify">
          <i class="el-icon-plus" style="color:blue;font-size:24px;"></i>
          <span slot="title">新建</span>
        </el-menu-item>
      </el-menu>
    </div>
    <div v-show="openform" style="position: fixed;bottom: 0px;left: 0px;width: 20%;">
      <newclassify @hidecreateform="closeform"></newclassify>
    </div>
  </div>
</template>
<script>
import newclassify from "./Classify/Newclassify";
export default {
  components: {
    newclassify
  },
  computed: {
    username: function() {
      return this.$store.getters.getname;
    },
    classify: function() {
      return this.$store.getters.getclassify;
    },
    todolist: function() {
      return this.$store.getters.gettodolist;
    },
    classifycount: function() {
      return this.todolist.reduce(function(total, item) {
        if (total[item.type]) {
          total[item.type] += 1;
        } else {
          total[item.type] = 1;
        }
        return total;
      }, {});
    }
  },
  data() {
    return {
      openform: false
    };
  },
  methods: {
    menuselect: function(index) {
      this.$store.commit("setselect", index);
    },
    menuitemclk: function(event) {
      // debugger;
    },
    createclassify: function() {
      this.openform = true;
    },
    closeform: function() {
      this.openform = false;
    }
  }
};
</script>
<style scoped>
.user {
  padding: 10px;
  display: flex;
  align-items: center;
  border-bottom: 1px solid rgb(224, 230, 237);
  height: 65px;
}
</style>


