<template>
  <el-card class="myform">
    <el-form ref="form" :model="form" :rules="rules" label-width="80px">
      <el-form-item label="内容" prop="label">
        <el-input v-model="form.label"></el-input>
      </el-form-item>
      <el-form-item label="补充" prop="labelmore">
        <el-input v-model="form.labelmore"></el-input>
      </el-form-item>
      <el-form-item label="时间" prop="data">
        <el-date-picker v-model="form.data" type="datetime" placeholder="选择日期时间"></el-date-picker>
      </el-form-item>
      <el-form-item label="类别" prop="type">
        <el-select v-model="form.type" placeholder="请选择所属类别">
          <el-option
            v-for="(item, index) in classify"
            :label="item.label"
            :value="item._id"
            v-bind:key="index"
          ></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="重要性" prop="important">
        <el-switch v-model="form.important"></el-switch>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="submitForm('form')">立即创建</el-button>
        <el-button @click="resetForm('form')">取消</el-button>
      </el-form-item>
    </el-form>
  </el-card>
</template>
<script>
export default {
  computed: {
    classify: function() {
      return this.$store.getters.getclassify;
    }
  },
  data() {
    return {
      form: {
        label: "",
        labelmore: "",
        data: new Date(),
        type: "",
        important: false
      },
      rules: {
        label: [{ required: true, message: "请输入内容", trigger: "blur" }],
        data: [{ required: true, message: "请选择时间", trigger: "blur" }],
        type: [{ required: true, message: "请选择类型", trigger: "blur" }]
      }
    };
  },
  methods: {
    submitForm(formName) {
      this.$refs[formName].validate(
        function(valid) {
          if (valid) {
            this.createtodo();
          } else {
            console.log("error submit!!");
            return false;
          }
        }.bind(this)
      );
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
      this.$emit("hidecreateform");
    },
    createtodo: function() {
      var url = this.$appconfig.API.addtodo;
      this.$http
        .get(url, {
          params: {
            belong: this.$store.getters.getuserid,
            type: this.form.type,
            content: this.form.label,
            note: this.form.labelmore,
            createtime: this.form.data,
            isimportant: this.form.important
          }
        })
        .then(
          function(response) {
            if (response.data.status == 1) {
              this.open("创建失败");
            } else {
              this.open("创建成功");
              this.$store.commit("addtodolist", response.data.data);
              this.$emit("hidecreateform");
            }
          }.bind(this)
        )
        .catch(function(error) {}.bind(this))
        .then(
          function() {
            // always executed
          }.bind(this)
        );
    },
    open(message) {
      this.$notify({
        title: "消息",
        message: message,
        position: "bottom-left"
      });
    }
  }
};
</script>
<style scoped>
.myform .el-form-item {
  width: 45%;
  display: inline-block;
  text-align: left;
}
</style>


