<template>
  <div class="mark">
    <div class="message">{{ message }}</div>
    <div class="buttongroup">
      <el-button type="primary" class="el-icon-sort">排序</el-button>
      <el-button type="primary" class="el-icon-more"></el-button>
    </div>
  </div>
</template>
<script>
export default {
  computed: {
    message: function(){
      return this.$store.getters.getcurrentlabel;
    }
  },
  data() {
    return {
    };
  }
};
</script>
<style scoped>
.mark {
  height: 100%;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  background-image: url("/static/assets/mark.png");
  background-size: 100%;
}
.message {
  color: #fff;
  font-size: 32px;
  color: #fff;
  font-size: 48px;
  padding: 10px;
}
.buttongroup {
  padding: 10px;
}
</style>


