<template>
  <el-container class="con">
    <el-aside>
      <classify></classify>
    </el-aside>
    <el-container>
      <el-header>
        <marked></marked>
      </el-header>
      <el-main>
        <tablelist></tablelist>
      </el-main>
    </el-container>
  </el-container>
</template>

<script>
import classify from "./Info/Classify";
import marked from "./Info/Marked";
import tablelist from "./Info/Tablelist";
export default {
  name: "Info",
  components: {
    classify,
    marked,
    tablelist
  },
  data() {
    return {};
  },
  methods: {
    getClassify: function() {
      var url = this.$appconfig.API.getclassify;
      this.$http
        .get(url, {
          params: {
            belong: this.$store.getters.getuserid
          }
        })
        .then(
          function(response) {
            if (response.data.status == 1) {
              console.log(response.data.message);
            } else {
              this.$store.commit("setclassify", response.data.data);
            }
          }.bind(this)
        )
        .catch(function(error) {}.bind(this))
        .then(
          function() {
            // always executed
          }.bind(this)
        );
    },
    getAgency: function() {
      var url = this.$appconfig.API.findtodo;
      this.$http
        .get(url, {
          params: {
            belong: this.$store.getters.getuserid
          }
        })
        .then(
          function(response) {
            if (response.data.status == 1) {
              console.log(response.data.message);
            } else {
              this.$store.commit("settodolist", response.data.data);
            }
          }.bind(this)
        )
        .catch(function(error) {}.bind(this))
        .then(
          function() {
            // always executed
          }.bind(this)
        );
    }
  },
  mounted: function() {
    if (this.$store.getters.gettoken == "") {
      if (window.localStorage.getItem("userinfo")) {
        this.$store.commit(
          "setname",
          JSON.parse(window.localStorage.getItem("userinfo"))
        );
        this.getClassify();
        this.getAgency();
      } else {
        this.$router.push({ path: "/login" });
      }
    } else {
      this.getClassify();
      this.getAgency();
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.con {
  width: 100%;
  height: 100%;
}
.el-header {
  background-color: rgb(130, 159, 244);
  color: #333;
  text-align: center;
}
.el-header {
  height: 25% !important;
  padding: 0;
}
.el-aside {
  background-color: #fff;
  color: #333;
  text-align: center;
  width: 20% !important;
  text-align: left;
  border: 1px solid rgb(224, 230, 237);
}

.el-main {
  background-color: #fff;
  color: #333;
  text-align: center;
  padding: 0;
}
</style>



