<template>
  <el-card>
    <el-form ref="form" :model="form" :rules="rules" label-width="80px">
      <el-form-item label="类别名称" prop="label">
        <el-input v-model="form.label"></el-input>
      </el-form-item>
      <el-form-item label="类别颜色" prop="color">
        <el-color-picker v-model="form.color"></el-color-picker>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="submitForm('form')">立即创建</el-button>
        <el-button @click="resetForm('form')">取消</el-button>
      </el-form-item>
    </el-form>
  </el-card>
</template>
<script>
export default {
  data() {
    return {
      form: {
        label: "",
        color: ""
      },
      rules: {
        label: [{ required: true, message: "请输入类别名称", trigger: "blur" }],
        color: [{ required: true, message: "请选择颜色", trigger: "change" }]
      }
    };
  },
  methods: {
    submitForm(formName) {
      this.$refs[formName].validate(
        function(valid) {
          if (valid) {
            this.createClassify();
          } else {
            console.log("error submit!!");
            return false;
          }
        }.bind(this)
      );
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
      this.$emit("hidecreateform");
    },
    createClassify() {
      var url = this.$appconfig.API.addclassify;
      this.$http
        .get(url, {
          params: {
            belong: this.$store.getters.getuserid,
            label: this.form.label,
            color: this.form.color
          }
        })
        .then(
          function(response) {
            if (response.data.status == 1) {
              this.open("创建失败");
            } else {
              this.open("创建成功");
              this.$store.commit("addclassify", response.data.data);
              this.$emit("hidecreateform");
            }
          }.bind(this)
        )
        .catch(
          function(error) {
          }.bind(this)
        )
        .then(
          function() {
            // always executed
          }.bind(this)
        );
    },
    open(message) {
      this.$notify({
        title: "消息",
        message: message,
        position: "bottom-left"
      });
    }
  }
};
</script>
<style scoped>
</style>


