// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import ElementUI from 'element-ui';
import 'element-ui/lib/theme-chalk/index.css';
import axios from 'axios';
import Vuex from 'vuex';
import { config } from "./config";

Vue.prototype.$appconfig = config;

Vue.config.productionTip = false
Vue.use(ElementUI);
Vue.use(Vuex);

const moduleUserInfo = {
  state: {
    token: "",
    username: "",
    userid: ""
  },
  getters: {
    getname: function () {
      return moduleUserInfo.state.username;
    },
    gettoken: function () {
      return moduleUserInfo.state.token;
    },
    getuserid: function () {
      return moduleUserInfo.state.userid;
    }
  },
  mutations: {
    setname: function (state, data) {
      state.token = data.token;
      state.username = data.user[0].username;
      state.userid = data.user[0]._id;
    }
  },
  actions: {
    asyncsetname: function (ctx, name) {
      setTimeout(() => {
        ctx.commit('setname', name);
      }, 1000);
    }
  }
}
const moduleClassify = {
  state: {
    select: -1,
    classify: [],
    label: "全部",
    id: ""
  },
  getters: {
    getclassify: function () {
      return moduleClassify.state.classify;
    },
    getcurrentlabel: function () {
      return moduleClassify.state.label;
    },
    getcurrentid: function () {
      return moduleClassify.state.id;
    }
  },
  mutations: {
    setselect: function (state, select) {
      state.select = select;
      state.label = state.classify[state.select].label;
      state.id = state.classify[state.select]._id;
    },
    setclassify: function (state, classify) {
      state.classify = classify;
    },
    addclassify: function (state, classify) {
      state.classify.push(classify);
    },
    remove: function (state, index) {
      state.classify.splice(index, 1);
    }
  },
  actions: {
    asyncremove: function (ctx, index) {
      setTimeout(() => {
        ctx.commit("remove", index)
      }, 1000);
    }
  }
}
const moduleAgency = {
  state: {
    todolist: []
  },
  getters: {
    gettodolist: function () {
      return moduleAgency.state.todolist;
    }
  },
  mutations: {
    settodolist: function (state, todolist) {
      state.todolist = todolist;
    },
    changefinish: function (state, id) {
      var it = state.todolist.find(function (item) {
        return item._id == id;
      });
      it.isfinish = !it.isfinish;
    },
    addtodolist: function (state, item) {
      state.todolist.push(item);
    }
  },
  actions: {}
}

var store = new Vuex.Store({
  modules: {
    user: moduleUserInfo,
    classify: moduleClassify,
    data: moduleAgency
  }
});

Vue.prototype.$http = axios

/* eslint-disable no-new */
new Vue({
  el: '#app',
  store,
  router,
  components: { App },
  template: '<App/>'
})



