<template>
  <div class="Login">
    <div class="form">
      <el-card class="box-card">
        <el-image src="/static/assets/logo.png" fit="fill">
          <div slot="placeholder" class="image-slot">
            加载中
            <span class="dot">...</span>
          </div>
          <div slot="error" class="image-slot">
            <i class="el-icon-picture-outline"></i>
          </div>
        </el-image>
        <el-input v-on:keyup.enter.native="submit" placeholder="用户名" v-model="username"></el-input>
        <el-input
          v-on:keyup.enter.native="submit"
          placeholder="密码"
          v-model="password"
          type="password"
        ></el-input>
        <div v-show="messageshow" class="message">
          <i class="el-icon-warning-outline"></i>
          {{ message }}
        </div>
        <el-row>
          <el-button type="primary" v-on:click="submit">登录</el-button>
          <el-button type="primary" v-on:click="signup">注册</el-button>
        </el-row>
      </el-card>
    </div>
  </div>
</template>

<script>
export default {
  name: "Login",
  data() {
    return {
      username: "",
      password: "",
      message: "message",
      messageshow: false
    };
  },
  methods: {
    submit: function() {
      if (this.username && this.password) {
        this.signin();
      } else {
        this.message = "用户名和密码不能为空";
        this.messageshow = true;
      }
    },
    signin: function() {
      var url = this.$appconfig.API.signin;
      this.$http
        .get(url, {
          params: {
            username: this.username,
            password: this.password
          }
        })
        .then(
          function(response) {
            if (response.data.status == 1) {
              this.message = response.data.message;
              this.messageshow = true;
            } else {
              window.localStorage.setItem("userinfo", JSON.stringify(response.data.data));
              this.$store.commit("setname", response.data.data);
              this.$router.push({ path: "/info" });
            }
          }.bind(this)
        )
        .catch(function(error) {
          this.$router.go(0);
        }.bind(this))
        .then(function() {
          // always executed
        }.bind(this));
    },
    signup: function() {
      this.$router.push({ path: "/signup" });
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.Login {
  width: 100%;
  height: 100%;
  background: #fff;
}
.form {
  width: 30%;
  min-width: 370px;
  height: 100%;
  background: #eee;
}
.box-card {
  width: 80%;
  margin: auto;
  transform: translate(0px, calc(25vh));
}
.el-input {
  margin-top: 20px;
  margin-bottom: 10px;
}
.el-button {
  margin: 0px 70px 0px 50px;
}
.el-row {
  margin: 10px 0px;
}
.el-image {
  width: 100px;
  height: 100px;
}
.image-slot {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  background: #f5f7fa;
  color: #909399;
  font-size: 20px;
}
.message{
  font-size: 12px;
}
</style>



